export default {
	data() {
		return {
			videoTitle: '',
			videoDes: '',
			src: ''
		};
	},
	methods: {
		addV() {
			var self = this;
			uni.chooseVideo({
				count: 1,
				sourceType: ['album', 'camera'],
				success: function(res) {
					console.log(res.tempFilePath);
					self.src = res.tempFilePath;
				}
			});
		},
		send(){
			if(this.videoTitle.length==0){
				uni.showToast({
					icon:'none',
					title:'请输入视频标题'
				})
				return;
			}
			if(this.videoDes.length==0){
				uni.showToast({
					icon:'none',
					title:'请输入视频描述'
				})
				return;
			}
		}
	}
};
